import emptyResult from '../../../../../images/empty_result.webp';
import s from './EmptyList.module.css';
export default function EmptyList() {
  return (
    <div className="wrapper">
      <img
        className={s.img}
        src={emptyResult}
        alt="emptyResult"
        width="600px"
      />
    </div>
  );
}
